import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { motion } from "framer-motion";
import { Link } from "react-router-dom"
import { Skeleton, notification } from "antd";
import "antd/dist/antd.min.css";

import axios from "axios";
import { AccountId, TransactionId, TransactionReceipt, TransferTransaction, Hbar, TokenAssociateTransaction } from "@hashgraph/sdk";



import Card from "./nftCard";
import BorrowArrow from "../../assets/images/Rectangle.png"
import Price from "../../assets/images/pricelogo.png";
import Sixsense from "../../assets/images/Sixsense.png";
import footerback from "../../assets/images/DOT1.png"

import { Tabs, Tab, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles"
import PropertiesCard from "./propertiesCard";

const useStyles = makeStyles((theme) => ({
  tabs:{
    minHeight: "300px"
  },
  indicator: {
    backgroundColor: 'red', // Customize the color of the tab underline here
    height: "6px"
  },
}));

const uri = `https://testnet.mirrornode.hedera.com/api/v1`;
const instance = axios.create({
  baseURL: uri,
});

const ownerId = "0.0.5738042"
 


const Buy = (props) => {
  const classes = useStyles();
  const [nft, setNft] = useState(null);
  const [value, setValue] = useState(1);
  const [propertiy, setPropertiy] = useState(4);
  useEffect(() => {
    props.setBuy(true)
  })
  const { tokenId, name, serialNumber } = useParams();
  const { minted, setMinted, topic, accountId, hash, weaponNfts } = props;

  const nfts = weaponNfts

  //Get NFT details with accountId and tokenId from Database
  useEffect(() => {
    const arr = {
      name: name,
      tokenId: tokenId
    }

    axios.post("https://v3market.net:8000/users/getcid", arr).then(res => {
      console.log(res.data)
      setNft(res.data)
    })
  }, [name, tokenId])


  // handle Tab control
  const handleTabChange = (event, newValue) => {
    setValue(newValue);
  }

  // when finish the purchasing, send wallet nfts to backend 
  const Sendnft = (metadata) => {
    var arr = {
      accountId: accountId.toString(),
      metadata: metadata,
    };
    if (metadata.length > 0) {
      axios.post("https://v3market.net:8000/users/nftsave", arr).then((res) => {
        if (res.data === "success") alert("success");
        else if (res.data === "Already send") alert("Already send");
      });
    }
  };

  async function isNFTAssociatedWithWallet(tokenId, accountId) {
    let check = false
    // cos
    try {
      let balance = await instance.get(`balances?account.id=${accountId}`);
      const tokens = balance.data.balances[0].tokens;
      for (var i = 0; i < tokens.length; i++) {
        if (tokens[i].token_id === tokenId) check = true

      }
      return check
    } catch (err) {
    }
  }

  async function handleClickMint(tokenId) {
    if (accountId) {
      const isAssociated = await isNFTAssociatedWithWallet(tokenId, accountId)
      if (isAssociated) {
        const transferTx = transferHbar(accountId, ownerId);
        try {
          const hbartransferReponse = await sendTransaction(transferTx);
          if (hbartransferReponse) {
            const tokenMinTx = transferNFT(tokenId, accountId, ownerId);
            try {
              const tokenMintResponse = await sendNftTransaction(tokenMinTx);
              if (tokenMintResponse) {
                setMinted(!minted)
                Sendnft(nfts)
              }
            } catch (err) {
              console.log(err)
              const retransfer = transferHbar(ownerId, accountId)
              try {
                const hbarretransferResponse = await sendTransaction(retransfer);
              } catch (err) {
                console.log(err);
              }
            }
          }
        } catch (err) {
          console.log(err)
        }

      } else {
        const associateTx = associateNFT(accountId);

        try {
          const associateNFTResponse = await sendTransaction(associateTx);

          if (associateNFTResponse) {
            const transferTx = transferHbar(accountId, ownerId);
            try {
              const hbartransferReponse = await sendTransaction(transferTx);
              if (hbartransferReponse) {
                const tokenMinTx = transferNFT(tokenId, accountId, ownerId);
                try {
                  const tokenMintResponse = await sendNftTransaction(tokenMinTx);
                  if (tokenMintResponse) {
                    setMinted(!minted)
                    Sendnft(nfts)
                  }
                } catch (err) {
                  console.log(err)
                  const retransfer = transferHbar(ownerId, accountId)
                  try {
                    const hbarretransferResponse = await sendTransaction(retransfer);
                  } catch (err) {
                    console.log(err);
                  }
                }
              }
            } catch (err) {
              console.log(err)
            }
          }
        } catch (err) {
          console.log(err);
        }
      }
    } else {
      alert("Wallet Conntect First")
    }


  }


  async function sendTransaction(tx, sign = false) {
    let response, hashConnectTxBytes;
    hashConnectTxBytes = sign ? (
      makeBytes(tx, accountId)
    ) : (
      tx.toBytes()
    );

    response = await hash?.sendTransaction(
      topic,
      {
        topic: topic,
        byteArray: hashConnectTxBytes,
        metadata: {
          accountToSign: accountId,
          returnTransaction: false,
        }
      }
    )

    if (response?.receipt) {
      return TransactionReceipt.fromBytes(
        response.receipt
      )
    } else {
      throw new Error("No transaction recipt found")
    }
  }
  async function sendNftTransaction(tx, sign = false) {
    let response, hashConnectTxBytes;
    hashConnectTxBytes = sign ? (
      makeBytes(tx, ownerId)
    ) : (
      tx.toBytes()
    );

    response = await hash?.sendTransaction(
      topic,
      {
        topic: topic,
        byteArray: hashConnectTxBytes,
        metadata: {
          accountToSign: ownerId,
          returnTransaction: false,
        }
      }
    )

    if (response?.receipt) {
      return TransactionReceipt.fromBytes(
        response.receipt
      )
    } else {
      throw new Error("No transaction recipt found")
    }
  }
  function makeBytes(trans, signingAcctId) {
    const transId = TransactionId.generate(signingAcctId);

    trans.setTransactionId(transId);
    trans.setNodeAccountIds([new AccountId(3)]);

    trans.freeze();

    const transBytes = trans.toBytes();

    return transBytes
  }


  function transferNFT(tokenId, accountId, ownerId) {
    const txID = TransactionId.generate(ownerId);
    console.log("SerialNumber: ", serialNumber, tokenId, accountId, ownerId)
    const transferTransaction = new TransferTransaction()
      .setTransactionId(txID)
      .addNftTransfer(tokenId, serialNumber, ownerId, accountId)
      .setNodeAccountIds([new AccountId(3)])
      .freeze()

    return transferTransaction
  }

  function transferHbar(accountId, ownerId) {
    const txID = TransactionId.generate(accountId);

    const transferTransaction = new TransferTransaction()
      .setTransactionId(txID)
      .addHbarTransfer(accountId, new Hbar(-nft.price))
      .addHbarTransfer(ownerId, new Hbar(nft.price))
      .setNodeAccountIds([new AccountId(3)])
      .freeze()


    return transferTransaction;
  }

  function associateNFT(accountId) {
    const txID = TransactionId.generate(accountId);
    const tokenIds = [tokenId];
    const associateTransaction = new TokenAssociateTransaction()
      .setTransactionId(txID)
      .setTokenIds(tokenIds)
      .setAccountId(accountId)
      .setNodeAccountIds([new AccountId(3)])
      .freeze();

    return associateTransaction;
  }

  if (nft === null) {
    return <div></div>
  } else {
    return (
      <div>
        <motion.section
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ ease: "easeInOut", duration: 0.9, delay: 0.2 }}
          className="container mx-auto mt-7">
          <Link to="/"><img src={BorrowArrow} /></Link>

          <div className="grid  lg:grid-cols-2 md:grid-cols-1 mt-6 sm:gap-4 sm:grid-cols-1 mb-20">
            <div className="flex justify-center items-center">
              <Card>
                <img
                  src={"https://ipfs.io/ipfs/" + nft.image}
                  alt="nft img"
                  className="shadow-2xl "
                  style={{width: "300px", height: "450px", objectFit: "cover", clipPath: "polygon(0 0, 100% 0, 100% 90%, 90% 100%, 0 100%)" }}
                />
              </Card>
            </div>

            <div className="lg:w-3/4 px-10 sm:px-0 ">
              <div
                className="
                text-5xl
                pt-10
                font-black

                        "
                style={{ color: "black" }}
              >
                {nft.name.toUpperCase()}
              </div>
              <div
                className="font-general-regular 
                          sm:text-left text-black
                          text-md
                          font-bold
                          sm:text-sm 
                          pt-3
                        ">
                {nft.description.toUpperCase()}
              </div>
              <div
                className="font-general-regular 
                        text-left
                        text-white
                        text-sm
                        sm:text-xl
                        pt-10
                        font-bold
                        ">
                Price
              </div>
              <div className="flex h-20" >
                <img src={Price} className="mr-5"    />
                <h1 className="text-5xl font-bold pt-3" style={{ color: "black" }}>{nft.price}</h1>
              </div>
              <div className="  mt-10">
                <button
                  className=" sm:w-full h-10   font-general-medium 
                                      rounded-md shadow-sm text-md text-white w-full font-bold text-xl mr-10
                                    " style={{ backgroundColor: '#FF0000' }}
                  onClick={() => handleClickMint(tokenId)}
                >
                  PURCHASE
                </button>
                {/* <button
                  className=" sm:w-full h-10 font-general-medium 
                                      rounded-md shadow-sm text-md text-black w-full font-bold text-xl 
                                    "
                  style={{ border: "2px solid black" }}
                // onClick={() => buyNFT()} 
                >
                  SAVE
                </button> */}
              </div>
            </div>
          </div>

          <Tabs
            value={value}
            variant="fullWidth"
            onChange={handleTabChange}
            TabIndicatorProps={{
              className: classes.indicator,
            }}
          >
            <Tab value={1} label={<Typography variant="body1" style={{ fontWeight: 'bold', fontSize: '16px' }}>
              BUNDLE
            </Typography>} />
            <Tab value={2} label={<Typography variant="body1" style={{ fontWeight: 'bold', fontSize: '16px' }}>
              PROPERTIES
            </Typography>} />

          </Tabs>
          {value === 1 && <div className="grid grid-cols-1 lg:grid-cols-2 md:grid-cols-1 sm:gap-10 bg-black pt-16 pb-16 lg:pl-32 px-20" >
            <div className="sm:pb-1 pb-10">
              <div
                className="
                text-white
                text-3xl
                font-bold
                        ">
                {nft.name}
              </div>
              <div
                className="font-general-regular 
                          sm:text-left text-white
                          text-white
                          text-md
                          sm:text-sm
                           
                          
                        ">
                {nft.description}
              </div>
            </div>
            <div className="">
              <img src={Sixsense} className="w-80 sm:h-32 h-20" style={{ width: "300px", height: "120px" }} />
            </div>
          </div>}
          {value === 2 && <div className="grid sm:grid-cols-1 md:grid-cols-1 lg:grid-cols-2 p-10 " style={{ backgroundImage: `url(${footerback})`, backgroundColor: "black" }}>
            <div className="grid grid-cols-2 lg:grid-cols-2 xl:grid-cols-4 py-3 sm:gap-2 sm:grid-cols-4  pt-8 pb-8">
              {propertiy === 0 && (
                <>
                  <Skeleton.Button active size="large" shape="default" />
                  <Skeleton.Button active size="large" shape="default" />
                  <Skeleton.Button active size="large" shape="default" />
                  <Skeleton.Button active size="large" shape="default" />
                </>
              )}
              <PropertiesCard
                propertiy={"WeaponType"}
                value={nft.weaponType}
                key={1}
              />
              <PropertiesCard
                propertiy={"Control"}
                value={nft.Control}
                key={2}
              />
              <PropertiesCard
                propertiy={"RPM"}
                value={nft.RPM}
                key={3}
              />
              <PropertiesCard
                propertiy={"Range"}
                value={nft.Range}
                key={4}
              />

            </div>
            <div className="px-8 sm:px-30 md:px-40 lg:px-20 ">

              <div
                className="font-general-regular 
                      text-left 
                      text-white
                      text-sm
                      sm:text-xl
                      pt-10
                      font-bold
                      ">
                Details


              </div>
              <div
                className="grid md:grid-cols-2 lg:grid-cols-2 sm:grid-cols-2 grid-cols-2
                        ">
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      ">
                  Token Id :{" "}
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      
                      ">
                  <Link
                    to={`https://hashscan.io/testnet/token/${tokenId}`}
                    className="text-blue-500
                        dark:text-blue-500
                        dark:hover:text-blue-300"
                    target="_blank"
                    rel="noreferrer">
                    {tokenId}
                  </Link>
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      
                      ">
                  SerialNumber :{" "}
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      
                      ">
                  {serialNumber}
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      ">
                  Token Standard :
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      ">
                  HTS
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      ">
                  Blockchain :
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      ">
                  Hedera
                </div>
              </div>
            </div>
          </div>}
        </motion.section>


      </div>

    );
  }

};

export default Buy;


