import { useEffect } from "react";
import Banner from "../Banner";

const Marketplace = (props) => {
  useEffect(() => {
    props.setBuy(false)
  })
  return (
    <div className="">
      {/* marketplace body */}
      <Banner hash={props.hash}  topic={props.topic} accountId={props.accountId} minted={props.minted} setMinted={props.setMinted} weaponNfts={props.weaponNfts} size={props.size} setWeaponNfts={props.setWeaponNfts} setSize={props.setSize} />
    </div>
  );
};

export default Marketplace;
